.news .headline {
    .leads {
        grid-column: 2 / span 3;
        .post-title {
            font-weight: 600; }
        .left {
           grid-column: 1 / span 2 ;
           .post-title {
                font-size: 1.05em; } }
        .right {
            grid-column: 4 / span 8;
            .post-title {
                font-size: 1.9em; } } }
    .feats {
        .flex-row {
            width: 100%; }
        .meta {
            width: 100%; }
        .thumb {
            width: 90px;
            height: 90px;
            img {
                aspect-ratio: 1 / 1; } } } }

